:root {
  --font-inter: "Inter", "Poppins", Arial, Helvetica, sans-serif;
  --spacer: 1rem;
  --limeGreen: #1db489;
  --brightRed: #dc414c;
  --facebook: #198ff5;
  --twitter: #1ca0f2;
  --instagram-start: #fdc468;
  --instagram-end: #df4996;
  --youtube: #c4032a;
  --toggle-bg-start: #378fe6;
  --toggle-bg-end: #3eda82;
  --toggle: #aeb3cb;
  --dark-bg: #1e202a;
  --dark-top-bg: #1f212e;
  --dark-card: #252a41;
  --dark-text-1: #8b97c6;
  --dark-text-2: #fff;
  --light-bg: #fff;
  --light-top-bg: #f5f7ff;
  --light-card: #f0f2fa;
  --light-text-1: #63687e;
  --light-text-2: #1e202a;
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  background: var(--dark-bg);
  color: var(--dark-text-2);
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
  height: auto;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
html {
  font-size: 100%;
}

body {
  font-family: var(--font-inter);
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
}

h1,
h2,
h3,
h4 {
  line-height: 1.1;
}

.container {
  max-width: 70em;
  width: 90%;
  margin: 0 auto;
}

.header {
  display: flex;
  flex-direction: column;
  padding: 1.5em 0;
  gap: 1em;
}
.header__title {
  font-weight: 700;
  border-bottom: 1px solid var(--dark-text-1);
  padding-bottom: 1.5em;
}
.header__title h1 {
  font-size: 2rem;
}
.header__title span {
  color: var(--dark-text-1);
}
@media screen and (min-width: 40.5em) {
  .header {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 2em 0;
  }
  .header__title {
    border: none;
    padding-bottom: 0;
  }
}

.toggle {
  border: none;
  display: flex;
  align-items: center;
  gap: 1em;
  padding: 0.5em 0 0;
}
.toggle span {
  color: var(--dark-text-1);
  font-weight: 700;
}
.toggle__wrapper {
  position: relative;
  border-radius: 150vw;
  background-image: linear-gradient(to right, var(--toggle-bg-start), var(--toggle-bg-end));
  overflow: hidden;
}
.toggle input[type=radio] {
  transform: translateX(100em);
  padding: 0;
  margin: 0 0.25em;
}
.toggle label {
  position: absolute;
  top: 0;
  width: 50%;
  height: 100%;
  opacity: 0.25;
}
.toggle label:first-child {
  left: 0;
}
.toggle label:last-child {
  right: 0;
}
.toggle__switcher {
  position: absolute;
  width: 1.15rem;
  height: 1.15rem;
  background: var(--dark-bg);
  top: 50%;
  left: 0.1875rem;
  transform: translateY(-50%);
  border-radius: 50%;
  pointer-events: none;
}
@media screen and (min-width: 40.5em) {
  .toggle {
    padding: 0;
  }
}

section {
  padding: 1em 0 0;
}

.overview {
  padding-bottom: 2em;
}
.overview__title {
  margin: 1em 0;
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2em;
  text-align: center;
}
@media screen and (min-width: 40.5em) {
  .cards {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
}
.cards .card {
  background: var(--dark-card);
}
.cards--1 .card {
  padding: 2em 3em;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2em;
  font-size: 0.875rem;
  position: relative;
}
.cards--1 .card::after {
  content: "";
  position: absolute;
  inset: -3px 0 0;
  border-radius: 4px;
  z-index: -2;
  background-color: currentColor;
}
.cards--1 .card:nth-child(1):after {
  background: var(--facebook);
}
.cards--1 .card:nth-child(2)::after {
  background: var(--twitter);
}
.cards--1 .card:nth-child(3)::after {
  background-image: linear-gradient(to right, var(--instagram-start), var(--instagram-end));
}
.cards--1 .card:nth-child(4)::after {
  background: var(--youtube);
}
.cards--1 .card__user {
  display: flex;
  align-items: center;
  gap: 0.5em;
  color: var(--dark-text-1);
  font-weight: 700;
}
.cards--1 .card__count {
  font-weight: 700;
}
.cards--1 .card__count p {
  font-size: 3rem;
  line-height: 1.1;
}
.cards--1 .card__count span {
  color: var(--dark-text-1);
  text-transform: uppercase;
  font-weight: 400;
  letter-spacing: 4px;
}
.cards--1 .card__report {
  display: flex;
  align-items: center;
  gap: 1em;
  color: var(--limeGreen);
  font-weight: 700;
}
.cards--1 .card:nth-child(4) .card__report {
  color: var(--brightRed);
}
.cards--2 .card {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 2em;
  row-gap: 1em;
  font-size: 0.875rem;
}
.cards--2 .card > span {
  line-height: 1;
  justify-self: start;
  align-self: start;
  font-weight: 700;
  color: var(--dark-text-1);
  text-align: left;
}
.cards--2 .card > img {
  justify-self: end;
  align-self: start;
}
.cards--2 .card p {
  justify-self: start;
  align-self: end;
  font-size: 1.75rem;
  font-weight: 700;
  line-height: 1;
}
.cards--2 .card__report {
  align-self: end;
  justify-self: end;
  display: flex;
  align-items: center;
  gap: 0.5em;
  color: var(--limeGreen);
  font-weight: 700;
}
.cards--2 .card:nth-child(2) .card__report {
  color: var(--brightRed);
}
.cards--2 .card:nth-child(7) .card__report {
  color: var(--brightRed);
}
.cards--2 .card:nth-child(8) .card__report {
  color: var(--brightRed);
}